// paneldynamic.templateErrorLocation: "Sets the location of an error message in relation to a question with invalid input. Choose between: \"Top\" - an error text is placed at the top of the question box; \"Bottom\" - an error text is placed at the bottom of the question box. The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Top\" by default)." => "Anger platsen fr ett felmeddelande i frhllande till en frga med ogiltiga indata. Vlj mellan: \"verst\" - en feltext placeras hgst upp i frgerutan; \"Nederst\" - en feltext placeras lngst ner i frgerutan. Alternativet \"rv\" tillmpar instllningen p sidniv (om den r instlld) eller p underskningsniv (\"Topp\" som standard)."
