// paneldynamic.state: "Choose from: \"Expanded\" - the panel is displayed in full and can be collapsed; \"Collapsed\" - the panel displays only the title and description and can be expanded; \"Locked\" - the panel is displayed in full and cannot be collapsed." => "Vlj mellan: \"Expanderad\" - panelen visas i sin helhet och kan fllas ihop; \"Komprimerad\" - panelen visar endast titel och beskrivning och kan expanderas; \"Lst\" - panelen visas i sin helhet och kan inte fllas ihop."
