// paneldynamic.templateTitle: "Type in a template for dynamic panel titles. Use {panelIndex} for the panel's general position and {visiblePanelIndex} for its order among visible panels. Insert these placeholders into the pattern to add automatic numbering." => "Skriv in en mall fr dynamiska panelrubriker. Anvnd {panelIndex} fr panelens allmnna position och {visiblePanelIndex} fr dess ordning bland de synliga panelerna. Infoga dessa platshllare i mnstret fr att lgga till automatisk numrering."
// paneldynamic.templateTabTitle: "Type in a template for tab titles. Use {panelIndex} for a panel's general position and {visiblePanelIndex} for its order among visible panels. Insert these placeholders into the pattern to add automatic numbering." => "Skriv in en mall fr flikrubriker. Anvnd {panelIndex} fr en panels allmnna position och {visiblePanelIndex} fr dess ordning bland synliga paneler. Infoga dessa platshllare i mnstret fr att lgga till automatisk numrering."
// paneldynamic.templateVisibleIf: "This setting allows you to control the visibility of individual panels within the dynamic panel. Use the `{panel}` placeholder to reference the current panel in your expression." => "Med den hr instllningen kan du styra synligheten fr enskilda paneler i den dynamiska panelen. Anvnd platshllaren {panel} fr att referera till den aktuella panelen i uttrycket."
// paneldynamic.titleLocation: "This setting is automatically inherited by all questions within this panel. If you want to override this setting, define title alignment rules for individual questions. The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Top\" by default)." => "Den hr instllningen rvs automatiskt av alla frgor i den hr panelen. Om du vill sidostta den hr instllningen definierar du regler fr rubrikjustering fr enskilda frgor. Alternativet \"rv\" tillmpar instllningen p sidniv (om den r instlld) eller p underskningsniv (\"Topp\" som standard)."
// paneldynamic.descriptionLocation: "The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Under the panel title\" by default)." => "Alternativet \"rv\" tillmpar instllningen p sidniv (om den r instlld) eller p underskningsniv (\"Under panelrubriken\" som standard)."
// paneldynamic.newPanelPosition: "Defines the position of a newly added panel. By default, new panels are added to the end. Select \"Next\" to insert a new panel after the current one." => "Definierar placeringen av en nyligen tillagd panel. Som standard lggs nya paneler till i slutet. Vlj \"Nsta\" fr att infoga en ny panel efter den nuvarande."
// paneldynamic.defaultValueFromLastPanel: "Duplicates answers from the last panel and assigns them to the next added dynamic panel." => "Duplicerar svar frn den sista panelen och tilldelar dem till nsta tillagda dynamiska panel."
// paneldynamic.keyName: "Reference a question name to require a user to provide a unique response for this question in each panel." => "Referera till ett frgenamn fr att krva att en anvndare anger ett unikt svar fr den hr frgan i varje panel."
// pehelp.defaultValueExpression: "This setting allows you to assign a default answer value based on an expression. The expression can include basic calculations - `{q1_id} + {q2_id}`, Boolean expressions, such as `{age} > 60`, and functions: `iif()`, `today()`, `age()`, `min()`, `max()`, `avg()`, etc. The value determined by this expression serves as the initial default value that can be overridden by a respondent's manual input." => "Med den hr instllningen kan du tilldela ett standardsvarsvrde baserat p ett uttryck. Uttrycket kan innehlla grundlggande berkningar  {q1_id} + {q2_id}, booleska uttryck, till exempel {age} > 60 och funktioner: 'iif()', 'today()', 'age()', 'min()', 'max()', 'avg()' osv. Vrdet som bestms av detta uttryck fungerar som det initiala standardvrdet som kan sidosttas av en svarandes manuella inmatning."
// pehelp.resetValueIf: "Use the magic wand icon to set a conditional rule that determines when a respondent's input is reset to the value based on the \"Default value expression\" or \"Set value expression\" or to the \"Default answer\" value (if either is set)." => "Anvnd trollstavsikonen fr att stlla in en villkorsregel som avgr nr en respondents inmatning terstlls till vrdet baserat p \"Standardvrdesuttryck\" eller \"Ange vrdeuttryck\" eller till vrdet \"Standardsvar\" (om ngot av dem r instllt)."
// pehelp.setValueIf: "Use the magic wand icon to set a conditional rule that determines when to run the \"Set value expression\" and dynamically assign the resulting value as a response." => "Anvnd trollstavsikonen fr att stlla in en villkorsregel som avgr nr \"Ange vrdeuttryck\" ska kras och dynamiskt tilldela det resulterande vrdet som ett svar."
// pehelp.setValueExpression: "Specify an expression that defines the value to be set when the conditions in the \"Set value if\" rule are met. The expression can include basic calculations - `{q1_id} + {q2_id}`, Boolean expressions, such as `{age} > 60`, and functions: `iif()`, `today()`, `age()`, `min()`, `max()`, `avg()`, etc. The value determined by this expression can be overridden by a respondent's manual input." => "Ange ett uttryck som definierar det vrde som ska anges nr villkoren i regeln \"Ange vrde om\" uppfylls. Uttrycket kan innehlla grundlggande berkningar  {q1_id} + {q2_id}, booleska uttryck, till exempel {age} > 60 och funktioner: 'iif()', 'today()', 'age()', 'min()', 'max()', 'avg()' osv. Vrdet som bestms av detta uttryck kan sidosttas av en svarandes manuella inmatning."
