// question.requiredIf: "Use the magic wand icon to set a conditional rule that prevents survey advancing or submission unless the question received an answer." => "Anvnd trollstavsikonen fr att stlla in en villkorsregel som frhindrar att underskningen gr vidare eller skickas in om inte frgan har ftt ett svar."
