// question.state: "Choose from: \"Expanded\" - the question box is displayed in full and can be collapsed; \"Collapsed\" - the question box displays only the title and description and can be expanded; \"Locked\" - the question box is displayed in full and cannot be collapsed." => "Vlj mellan: \"Expanderad\" - frgerutan visas i sin helhet och kan fllas ihop; \"Komprimerad\" - frgerutan visar endast titel och beskrivning och kan expanderas; \"Lst\" - frgerutan visas i sin helhet och kan inte fllas ihop."
// question.titleLocation: "Overrides title alignment rules defined on a panel, page or survey level. The \"Inherit\" option applies any higher-level settings (if set) or survey-level setting (\"Top\" by default)." => "sidostter regler fr justering av titlar som definierats p panel-, sid- eller underskningsniv. Alternativet \"rv\" tillmpar alla instllningar p hgre niv (om de r instllda) eller instllningar p underskningsniv (\"verst\" som standard)."
// question.descriptionLocation: "The \"Inherit\" option applies the survey-level setting (\"Under the question title\" by default)." => "Alternativet \"rv\" tillmpar instllningen p underskningsniv (\"Under frgans titel\" som standard)."
// question.errorLocation: "Sets the location of an error message in relation to the question with invalid input. Choose between: \"Top\" - an error text is placed at the top of the question box; \"Bottom\" - an error text is placed at the bottom of the question box. The \"Inherit\" option applies the survey-level setting (\"Top\" by default)." => "Anger platsen fr ett felmeddelande i frhllande till frgan med ogiltig inmatning. Vlj mellan: \"verst\" - en feltext placeras hgst upp i frgerutan; \"Nederst\" - en feltext placeras lngst ner i frgerutan. Alternativet \"rv\" tillmpar instllningen p underskningsniv (\"Topp\" som standard)."
