// question.width: "Sets the width of the question in proportion to other survey elements in the same line. Accepts CSS values (px, %, in, pt, etc.)." => "Anger frgans bredd i proportion till andra underskningselement p samma rad. Accepterar CSS-vrden (px, %, in, pt, etc.)."
// surveyvalidator.expression: "Use the magic wand icon to set a validation rule for the question." => "Anvnd trollstavsikonen fr att stlla in ett valideringsuttryck fr frgan."
// question.textUpdateMode: "Choose from: \"On lost focus\" - the value is updated when the input field loses focus; \"While typing\" - the value is updated in real-time, as users are typing. The \"Inherit\" option applies the survey-level setting (\"On lost focus\" by default)." => "Vlj mellan: \"Vid frlorat fokus\" - vrdet uppdateras nr inmatningsfltet tappar fokus; \"Medan du skriver\" - vrdet uppdateras i realtid nr anvndarna skriver. Alternativet \"rv\" tillmpar instllningen p underskningsniv (\"Vid frlorat fokus\" som standard)."
// question.url: "You can use any web service as a data source for multiple-choice questions. To populate choice values, enter the URL of the service providing the data." => "Du kan anvnda valfri webbtjnst som dataklla fr flervalsfrgor. Om du vill fylla i alternativvrden anger du URL:en fr den tjnst som tillhandahller data."
